"use client";
import Head from 'next/head';
import Link from 'next/link';
import Layout from '@/app/layout'; // Import Layout component
import { useState } from 'react';

export default function Home() {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: '',
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    setFormData({ ...formData, [e.target.id]: e.target.value });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      const response = await fetch('http://localhost:5000/send-email', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(formData),
      });

      const result = await response.text();
      alert(result); // Show success or error message
      setFormData({ name: '', email: '', message: '' }); // Clear the form

    } catch (error) {
      console.error('Error:', error);
      alert('An error occurred');
    }
  };

  return (
    <Layout>
      <div>
        <Head>
          <title>aCloudBridge</title>
          <link rel="icon" href="/favicon.ico" />
        </Head>

        <main className="min-h-screen bg-gray-900 text-white">
          {/* Hero Section */}
          <div className="relative h-[500px] bg-cover bg-center" style={{ backgroundImage: 'url(/images/my-image.jpg)' }}>
            <div className="absolute inset-0 bg-black opacity-50"></div>
            <div className="absolute inset-0 flex flex-col justify-center items-center text-center">
              <div className="flex flex-col items-center">
                <h1 className="text-4xl font-bold mb-4">aCloudBridge</h1>
                <p className="text-lg mb-4">A Bridge to the Cloud Technologies</p>
              </div>
              <Link href="/login" className="bg-[#487385] hover:bg-[#345c6b] text-white font-bold py-2 px-8 rounded">
                Login
              </Link>
            </div>
          </div>

          {/* Contact Form Section */}
          <div className="bg-[#102323] py-16 px-4">
            <div className="max-w-md mx-auto">
              <h2 className="text-3xl font-bold text-center mb-8">Contact Us</h2>
              <div className="border-b border-gray-700 w-16 mx-auto mb-8"></div>
              <p className="text-center mb-8">Drop us a line!</p>

              <form className="space-y-6" onSubmit={handleSubmit}>
                <div>
                  <input
                    type="text"
                    id="name"
                    className="mt-1 block w-full rounded-md bg-gray-900 border border-white text-white p-2"
                    placeholder="Full Name"
                    value={formData.name}
                    onChange={handleChange}
                  />
                </div>
                <div>
                  <input
                    type="email"
                    id="email"
                    className="mt-1 block w-full rounded-md bg-gray-900 border border-white text-white p-2"
                    placeholder="Email Address"
                    required
                    value={formData.email}
                    onChange={handleChange}
                  />
                </div>
                <div>
                  <textarea
                    id="message"
                    rows={4}
                    className="mt-1 block w-full rounded-md bg-gray-900 border border-white text-white p-2"
                    placeholder="Your Message"
                    value={formData.message}
                    onChange={handleChange}
                  ></textarea>
                </div>
                <div className="flex items-center">
                  <input type="checkbox" id="newsletter" className="rounded border-gray-700 text-indigo-600 focus:ring-indigo-500" />
                  <label htmlFor="newsletter" className="ml-2 block text-sm text-white">Sign up for our email list for updates, promotions, and more.</label>
                </div>
                <div>
                  <button
                    type="submit"
                    className="w-full flex justify-center py-2 px-4 border border-transparent rounded-md shadow-sm text-sm font-medium text-white bg-[#487385] hover:bg-[#345c6b] focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-[#487385]"
                  >
                    Send
                  </button>
                </div>
              </form>
            </div>
          </div>
        </main>
      </div>
    </Layout>
  );
}